<template>
  <div class="wrapper login-wrapper register-wrapper">
    <el-form
      v-if="!showAlignSend"
      class="register-form"
      auto-complete="off"
      :model="registerForm"
      :rules="registerRules"
      ref="registerForm"
      label-position="left"
    >
      <h3 class="title">注册</h3>
      <el-form-item prop="trueName">
        <span class="svg-container svg-container_register">
          <svg-icon name="icon-user"/>
        </span>
        <el-input
          name="trueName"
          type="text"
          v-model="registerForm.trueName"
          auto-complete="off"
          placeholder="填写真实姓名"
        />
      </el-form-item>
      <el-form-item prop="username">
        <span class="svg-container svg-container_register">
          <svg-icon name="email"/>
        </span>
        <el-input
          name="username"
          type="text"
          v-model="registerForm.username"
          auto-complete="off"
          placeholder="填写邮箱地址"
        />
      </el-form-item>
      <el-form-item prop="password">
        <span class="svg-container">
          <svg-icon name="password"></svg-icon>
        </span>
        <input type="password" tabindex="-1" style="position: absolute; top: -999px">
        <el-input
          name="password"
          :type="pwdType"
          v-model="registerForm.password"
          auto-complete="off"
          placeholder="设置6-20位数字或字母或符号为登录密码"
        ></el-input>
        <span class="show-pwd" @click="showPwd">
          <svg-icon v-if="pwdType=='password'" name="eye-open"/>
          <svg-icon v-else name="eye-close"/>
        </span>
      </el-form-item>
      <el-form-item prop="checkPass">
        <span class="svg-container">
          <svg-icon name="password"></svg-icon>
        </span>
        <el-input
          type="password"
          v-model="registerForm.checkPass"
          auto-complete="off"
          placeholder="请再次输入密码"
        ></el-input>
      </el-form-item>
      <el-form-item prop="phone">
        <span class="svg-container">
          <svg-icon name="phone"></svg-icon>
        </span>
        <el-input
          name="phone"
          type="text"
          @keyup.enter.native="handleRegister"
          v-model="registerForm.phone"
          auto-complete="off"
          placeholder="请输入手机号"
          maxlength="11"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button
          type="primary"
          style="width:100%;"
          :loading="loading"
          @click.native.prevent="handleRegister"
        >确定</el-button>
      </el-form-item>
      <div class="tips">
        <router-link to="/login" style="margin-right:20px;">已有账号，登录</router-link>
        <!-- <router-link to="/forgetpassword">忘记密码</router-link> -->
      </div>
    </el-form>
    <div v-if="showAlignSend" class="password-form align-part">
      <h5>密码的重置链接已发送</h5>
      <p>请打开邮箱，点击里面的内容链接重置密码！</p>
      <div>
        <span>{{passwordForm.username}}没有收到？</span>
        <el-button type="text">重新发送</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'register',
  data() {
    const validatePass = (rule, value, callback) => {
      const reg = /^1[34578]\d{9}$/;
      if (value === '') {
        callback(new Error('请输入手机号'));
      } else if (!reg.test(value)) {
        callback(new Error('请输入正确的手机号'));
      } else {
        callback();
      }
    };
    const validatePass2 = (rule, value, callback) => {
      console.log(value);
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.registerForm.password) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      registerForm: {
        username: '',
        password: '',
        checkPass: '',
        phone: '',
        trueName: ''
      },
      showAlignSend: false,
      registerRules: {
        trueName: [
          {
            required: true,
            message: '请输入真实姓名',
            trigger: 'blur'
          }
        ],
        username: [
          {
            required: true,
            message: '请输入邮箱地址',
            trigger: 'blur'
          }
        ],
        password: [
          {
            required: true,
            message: '请输入密码',
            trigger: 'blur'
          },
          {
            min: 6,
            max: 20,
            message: '6-20位数字或字母或符号',
            trigger: 'blur'
          }
        ],
        checkPass: [{validator: validatePass2, trigger: 'blur'}],
        phone: [{validator: validatePass, trigger: 'blur'}]
      },
      pwdType: 'password',
      loading: false
    };
  },
  methods: {
    showPwd() {
      if (this.pwdType === 'text') {
        this.pwdType = 'password';
      } else {
        this.pwdType = 'text';
      }
    },
    handleRegister() {
      this.loading = true;
      this.$refs.registerForm.validate(valid => {
        if (valid) {
          this.$store
            .dispatch('REGISTER', this.registerForm)
            .then(result => {
              this.loading = false;
              this.$message({
                type: 'success',
                message: '注册成功'
              });
              this.$router.push('/login');
            })
            .catch(err => {
              console.log(err);
              this.loading = false;
            });
        } else {
          console.log('error submit!!');
          this.loading = false;
          return false;
        }
      });
    }
  }
};
</script>

<style>
.register-wrapper .register-form {
  position: absolute;
  left: 0;
  right: 0;
  width: 520px;
  padding: 35px 35px 15px 35px;
  margin: 5vh auto 0;
  box-sizing: border-box;
}
</style>
